<template>
    <div id="Confirm" class="layout">
      <p class="tit">$confirm</p>
      <div class="base_box">
       <Button
          :butContent="''"
          @click="ConfirmFn()"
        />
      </div>
    </div>
</template>

<script>
import { getCurrentInstance } from "vue";
export default {
    setup(){
        const { proxy } = getCurrentInstance();
        const ConfirmFn = function () {
            proxy.h_confirm({
                title: "",
                content: ",",
                style:{
                    box:{
                        top:30,
                        bgColor:'#fff',
                        width :400,
                        padding :10,
                        borderRadius:0
                    },
                    title:{
                        // tittextAlign:'left',
                        titColor:'red',
                        // titlineHeight:30,
                        titfontSize:22,
                        titfontWeight:100
                    },
                    content:{
                        // contextAlign:'left',
                        // conColor:'blue',
                        // conlineHeight:22,
                        // confontSize:14,
                        // confontWeight:100
                    }
                    
                },
                buttonConfigs: {
                    yes: "",
                    no: ""
                },
            }) .then(() => {
                // 
                proxy.h_tips({
                    type:'success',
                    msg:"",
                    top:30,
                    time:1000
                })
            }).catch(() => {
                // 
                proxy.h_tips({
                    type:'info',
                    msg: " ",
                    top:30,
                    time:1000
                })
            });                
        }
        return{
            ConfirmFn
        }
    } 
}
</script>

