<template>
    <div id="Button" class="layout">
      <p class="tit">Button</p>
      <div class="cancelFloat">
            <Button
              :butContent="''"
              :type="'info'"
              :float="'l'"
              @click="butfn"
            />
            <Button
              :butContent="''"
              :type="'success'"
              :float="'l'"
              @click="butfn"
            />
            <Button
              :butContent="''"
              :type="'warning'"
              :float="'l'"
              @click="butfn"
            />
            <Button
              :butContent="''"
              :float="'l'"
              @click="butfn"
            />
            <Button
              :butContent="''"
              :type="'error'"
              :float="'l'"
              @click="butfn"
            />
      </div>
      <!--  -->
      <p class="tit">Button</p>
      <Formtable :contentData="contentData" />
      <p class="tit">Button</p>
      <Formtable 
        :titData ="titDataEvent"
        :contentData="contentDataEvent" 
      />
    </div>
    
</template>

<script>
import { getCurrentInstance } from "vue";
export default {
  setup(props) {
    const { proxy } = getCurrentInstance();
    const contentData = [
      ["Width", "", "String", "px", "auto"],
      ["BGcolor", "", "String", "", "#00a2ff"],
      ["FontColor", "", "String", "", "#fff"],
      ["fontSize", "", "Number", "", "16"],
      ["lineHeight", "", "Number", "", "30"],
      ["butContent", "", "String", "", ""],
      ["type", "", "String", "infosuccesswarningerrordefault", "default"],
      ["float", "", "String", "lr", "" ],
    ];
    const titDataEvent = [ "", "", ""]
    const contentDataEvent = [["click", "", "type "]]
    const butfn =function (type){
      // 
      proxy.h_tips({
          type:type,
          msg: type+ " ",
          top:30,
          time:1000
      })
    }
    return{
      contentData,
      titDataEvent,
      contentDataEvent,
      butfn
    }
  }
}
</script>

