<template>
    <div id="Pagination" class="layout">
      <p class="tit">1Pagination</p>
      <div class="base_box">
        <Button
              :butContent="''"
              :Width="'300px'"
              :BGcolor="'#fff'"
              :lineHeight="30"
              :FontColor="'#00f'"
              @click="setcurrentPage"
        />
       <Pagination
            :Count="bigCount"
            :currentPage="currentPage"
            @getCurrentPage="getpage"
       />
      </div>
    </div>
</template>

<script>
import { ref , getCurrentInstance } from "vue";
export default {
    setup(props) {
        const { proxy } = getCurrentInstance();
        const currentPage = ref(1)   //
        const bigCount = ref(30)
        const minCount = ref(20)
        // 1
        const getpage = function (val) {
            currentPage.value = val;
            // 
            proxy.h_tips({
                type:'success',
                msg: `${val}`,
                top:30,
                time:1000
            })
        }
        // 
        const setcurrentPage = function() {
            proxy.h_tips({
                type:'success',
                msg: `50`,
                top:30,
                time:1000
            })
            bigCount.value = 50
            currentPage.value = 1; //
        }
        return{
            currentPage,
            bigCount,
            minCount,
            getpage,
            setcurrentPage
        }
    }
   
 
}
</script>

