<template>
  <div class="suptopUibox" :class="{ suptopUi_navshow: suptopUi_navSwitch }">
    <!--  -->
    <div class="suptopUi_nav">
      <!--  -->
      <ScrollContainer>
        <a
          :href="'#/assembly/#'+nav.EN"
          class="navitem"
          @click="navChange(nav.EN)"
          :class="{ active: navitem == nav.EN }"
          v-for="nav in navlist"
          :key="nav.EN"
          >{{ nav.CH }}</a
        >
      </ScrollContainer>
    </div>
    <!--  -->
    <div class="suptopUi_components">
      <!--  -->
      <span
        class="suptopUi_navbtn iconfont icon-24px"
        @click="suptopUi_navSwitch = !suptopUi_navSwitch"
      ></span>
      <!--  -->
      <ScrollContainer >
        <!--  -->
        <assemblyList />
      </ScrollContainer>
    </div>
  </div>
</template>
<script>
import { onMounted, ref ,reactive} from "vue";
// 
import assemblyList from "./assembly_list";
import { UiList } from "../../components/component_module/index.js";
import { log } from 'util';
export default {
  components: {
    assemblyList,
  },
  setup(){
    const suptopUi_navSwitch = ref(false)  //
    const navitem = ref(null)              //
    const navlist = reactive(UiList)
    
    onMounted(()=>{
        hashChange()       // #
        watchUrlChange()   // url
    })
    //url => id
    const hashChange = function (){
      //  #/assembly/#Button    //Button
      //#id
       const loc = location.hash.split('#')[2] ?? 'explain' 
       navChange(loc)     //
    }
    // 
    const navChange = function (idName) {
      suptopUi_navSwitch.value = false; //
      navitem.value  = idName; //a
      //  a #url
      //  
      document.querySelector("#" + idName).scrollIntoView(true); //'#'+idName   #Drop     id = 'Drop'
      
    }
    // url
    const watchUrlChange = function () {
        //url
        if( ('onhashchange' in window) && ((typeof document.documentMode==='undefined') || document.documentMode==8)) {
            // onhashchange
            window.onhashchange = hashChange;  // TODOhash
        } else {
            console.log('url');
            // 
            // setInterval(function() {
            //     // hash iEwindow.location.hash
            //     var ischanged = isHashChanged();
            //     if(ischanged) {
            //         hashChange();  // TODOhash
            //     }
            // }, 150);
        }
    }
    
    
    return{
      suptopUi_navSwitch,
      navitem,
      navlist,
      navChange
    }
  }
};
</script>
<style lang="less" scoped>
/*  */
.suptopUibox {
  position: relative;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  .suptopUi_nav {
    width: 250px;
    height: 100%;
    position: absolute;
    padding-top: 10px;
    border-radius: 0px 5px 5px 0px;
    z-index: 888;
    top: 0;
    left: 0;
    box-sizing: border-box;
    overflow: auto;
    transition: 0.3s;
    background: rgb(255, 255, 255);
    border-right: 1px solid #ddd;
    .navitem {
      cursor: pointer;
      display: block;
      padding: 0 20px;
      line-height: 40px;
      height: 40px;
      text-decoration: none;
      transition: 0.3s;
      color: rgb(148, 148, 148);
      box-sizing: border-box;
      transition: 0.3s;
      overflow: hidden;
      -moz-user-select: none; /**/
      -webkit-user-select: none; /*webkit*/
      -ms-user-select: none; /*IE10*/
      -khtml-user-select: none; /**/
      user-select: none;
      &:hover {
        color: #000;
      }
      &.active {
        color: #409eff;
      }
    }
  }
  .suptopUi_components {
    width: 100%;
    height: 100%;
    padding-left: 250px;
    box-sizing: border-box;
    overflow: auto;
    transition: 0.3s;
    .suptopUi_navbtn {
      cursor: pointer;
      font-size: 20px;
      display: none;
      position: fixed;
      transition: 0.3s;
      z-index: 990;
      .content {
        height: 100%;
      }
    }
  }
}

.min .suptopUi_nav {
  width: 0;
}
.min .suptopUi_navshow .suptopUi_nav {
  width: 200px;
}
.min .suptopUi_navshow .suptopUi_navbtn {
  padding-left: 200px;
}

.min .suptopUi_components {
  padding-left: 0px;
}

.min .suptopUi_components .suptopUi_navbtn {
  width: 30px;
  display: block;
}
.min .suptopUi_components .suptopUi_navshow .suptopUi_navbtn {
  padding-left: 200px;
}
</style>
