<template>
  <div id="Tips" class="layout">
    <p class="tit">$tips</p>
    <Button
      :butContent="''"
      :float="'l'"
      :type="'success'"
      :Width="'40%'"
      @click="tipsFn()"
    />
    <Button
      :butContent="''"
      :float="'r'"
      :type="'success'"
      :Width="'40%'"
      @click="tipsFn1()"
    />
  </div>
</template>

<script>
import { getCurrentInstance } from "vue";
export default {
  setup(props, ctx) {
    const { proxy } = getCurrentInstance();
    // 
    const tipsFn = function () {
      // 
      proxy.h_tips({
        type: "success",
        msg: "",
        top: 100,
        time: 100,
      });
    };
    // 
    const tipsFn1 = function () {
      // 
      proxy.h_tips({
        type: "success",
        tit: "",
        msg: "",
        top: 30,
        time: 1000,
      });
    };
    return {
      tipsFn,
      tipsFn1,
    };
  },
};
</script>

